import React from 'react';
import { withRouter } from 'react-router';

// base page
import BasePage from '../../pages/base-page';
import './index.scss';
// components and containers

import { pathConstants } from 'constants/path-constants';
import ReceiptDetails from 'containers/receipt-details';
import Transactionlist from 'containers/transaction-list';
import BlockStatusBox from 'containers/status-count';
import BlockContainer from 'containers/block-explorer';
import Profile from 'containers/profile';
import BlockList from 'containers/block-list';
import TransactionDetails from 'containers/transaction-details';
import BlockDetail from 'containers/block-detail-list';
import BreadcrumbComponent from 'components/breadcrumb';
import * as Sockets from '../../sockets/socket';

// const APP_CONSTANTS = require('constants/app-constants')

