class BlockExplorer extends BasePage {
    // constructor(props) {
    //     super(props);
    // }

    componentDidMount() {
        debugger;
        Sockets.connectToAuthWebsocket();
    }

    render() {
        debugger;
        // const Home = ({ routes, params, children })
        return (
            <div className="blockexplorer ">

                {
                    this.props.location.pathname.includes(pathConstants.BLOCKEXPLORER) ?
                        <div >
                            <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }]} />
                            <div className="blockexplorer__content">
                                <BlockStatusBox />
                                <BlockContainer />
                            </div>
                        </div>

                        : this.props.location.pathname.includes(pathConstants.BLOCKLIST) ?
                            <div>
                                <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "block" }]} />
                                <div className="blockexplorer__content"><BlockList /></div>
                            </div>
                            : this.props.location.pathname.includes(pathConstants.RECEIPTDETAILS) ?
                                <div >
                                    <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "transactions" }, { 'name': "transaction details" }, { 'name': "receipt-details" }]} />
                                    <div className="blockexplorer__content">
                                        <ReceiptDetails />
                                    </div>
                                </div>
                                : this.props.location.pathname.includes(pathConstants.TRANSACTIONS) ?
                                    <div >
                                        <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "transactions" }]} />
                                        <div className="blockexplorer__content">
                                            <Transactionlist />
                                        </div>
                                    </div>
                                    : this.props.location.pathname.includes(pathConstants.TRANSACTION_DETAILS) ?
                                        <div>
                                            <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "transactions" }, { 'name': "transaction details" }]} />
                                            <div className="blockexplorer__content">
                                                <TransactionDetails />
                                            </div>
                                        </div>
                                        : this.props.location.pathname.includes(pathConstants.BLOCKDETAIL) ?
                                            <div>
                                                <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "block" }, { 'name': "block details" }]} />
                                                <div className="blockexplorer__content"><BlockDetail /></div>
                                            </div>

                                            : this.props.location.pathname.includes(pathConstants.PROFILE) ?
                                                <div>
                                                    <BreadcrumbComponent breadcrumbData={[{ 'name': "block-explorer" }, { 'name': "block" }, { 'name': "block-details" }]} />
                                                    <div className="blockexplorer__content"><Profile /></div>
                                                </div>
                                                : null


                }

            </div>
        );
    }

}
export default withRouter(BlockExplorer);



