import React from 'react';
import { withRouter } from 'react-router';

import './index.scss';



class CustomBox extends React.Component {
  


    render() {
        const { title, count, number } = this.props
        const hoverclass = this.props.onClick?'custom-box custom-box-hover':'custom-box';
        return (
            <div className={hoverclass} onClick={() => this.props.onClick(count, number)}>
                <h2>{title}</h2>
                {
                    count && count !== undefined ?
                        typeof this.props.onClick === 'function'?
                            <p>{count}</p>
                        :
                            <p>{count}</p>
                    :
                    <p>Loading..</p>
                }
            </div>
        );
    }
}
export default withRouter(CustomBox);

