import React from 'react';
import { withRouter } from 'react-router';

//nivo charts
import { ResponsiveLine } from '@nivo/line'

import './index.scss';
const APP_CONSTANTS = require('constants/app-constants')

class LineChart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            transactionGraph: this.props.dataSet,
            graphLegendProperty: APP_CONSTANTS.LINE_CHART_LEGENDS_PROPERTY,
            rightMargin:APP_CONSTANTS.RIGHT_MARGIN,
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }

    getDimensions() {
        const w = window;
        const d = document;
        const documentElement = d.documentElement;
        const body = d.getElementsByTagName('body')[0];
        const width = w.innerWidth || documentElement.clientWidth || body.clientWidth;
        const height = w.innerHeight || documentElement.clientHeight || body.clientHeight;

        return { width, height };
    }

    updateDimensions = () => {
        console.log(this.getDimensions().width)
        if (this.getDimensions().width < 728)
            this.setState({ graphLegendProperty: [],rightMargin:APP_CONSTANTS.RIGHT_MARGIN_MOBILE_VIEW });
        else
            this.setState({ graphLegendProperty: APP_CONSTANTS.LINE_CHART_LEGENDS_PROPERTY,
                rightMargin:APP_CONSTANTS.RIGHT_MARGIN });
    };


    componentWillReceiveProps(nextProps) {
        if (nextProps.dataSet !== this.state.transactionGraph)
            this.setState({ transactionGraph: nextProps.dataSet })
    }

    render() {
      
        return (
            <div className="graphParent">
                <ResponsiveLine
                    data={this.state.transactionGraph}
                    margin={{
                        "top": 20,
                        "right": this.state.rightMargin,
                        "bottom": 80,
                        "left": 50
                    }}
                    xScale={{
                        "type": "point"
                    }}
                    yScale={{
                        "type": "linear",
                        "stacked": false,
                        "min": "auto",
                        "max": "auto"
                    }}
                    curve={'monotoneX'}
                    colors={'nivo'}
                    minY="auto"
                    maxY="auto"
                    stacked={true}
                    axisBottom={{
                        "orient": "bottom",
                        "tickSize": 5,
                        "tickPadding": 5,
                        "tickRotation": 90,
                        "legend": "",
                        "legendOffset": 16,
                        "legendPosition": "middle"
                    }}
                    axisLeft={{
                        "orient": "left",
                        "tickSize": 5,
                        "tickPadding": 5,
                        "tickRotation": 0,
                        "legend": "count",
                        "legendOffset": -40,
                        "legendPosition": "middle"
                    }}
                    enableDotLabel={false}
                    dotSize={10}
                    dotColor="inherit:darker(0.3)"
                    dotBorderWidth={2}
                    dotBorderColor="#ffffff"
                    dotLabel="y"
                    dotLabelYOffset={-12}
                    animate={true}
                    motionStiffness={90}
                    motionDamping={15}
                    legends={this.state.graphLegendProperty}
                />
            </div>
        );
    }
}
export default withRouter(LineChart);
