import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col, Table, Button, message } from 'antd';
import moment from "moment";
import BarChart from 'components/barchart';
import LineChart from 'components/linechart';
import * as Utils from 'utils/util';

import './index.scss';
// Actions
import { default as GetBlocksActions } from "store/blockExplorer/actions";

import { pathConstants } from 'constants/path-constants';

// Actions
import { default as BlockExplorerActions } from "store/blockExplorer/actions";
// components

import Loader from 'components/loader';
import * as Sockets from '../../sockets/socket';

const { getTransactionData } = BlockExplorerActions;
const { getBlocks, getTransactions, getTransactionsGraph, getChainTransaction } = GetBlocksActions;
const APP_CONSTANTS = require('constants/app-constants')


class BlockExploreContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // loading: false,
            loadingForBlock: false,
            loadingForGraph: false,
            loadingForTransaction: false,
            blocksData: [],
            transactions: [],
            chaintransactions: [],
            transactionBarChart: [],
            transactionLineChart: []
        }
    }

    componentDidMount() {
       
        console.log('socket'+Sockets.socketConnection)
        
        console.log("BlockExploreContainer componentDidMount")
        this.setState({ loadingForBlock: true, loadingForGraph: true, loadingForTransaction: true });
        var self = this;
     
        Sockets.emitter.addListener('block', function (data) {
            console.log("BlockExploreContainer SOCKET DATA RECEIVED" + JSON.stringify(data));
            self.fetchWebSocketData(data);
        });

        Sockets.emitter.addListener('chart', function (data) {
            console.log("SOCKET DATA RECEIVED for Chart" + JSON.stringify(data));
            self.fetchSocketChartData(data);
        });

        Sockets.emitter.addListener('transactionInfo', function (data) {
            console.log("SOCKET DATA RECEIVED for transaction" +JSON.stringify(data));
            self.fetchSocketTransactionData(data);
        });

        Sockets.emitter.addListener('paramLogs', function (data) {
            console.log("SOCKET DATA RECEIVED for paramLogs" +JSON.stringify(data));
            // self.fetchSocketTransactionData(data);
        });
        

        this.props.getBlocks();
        //this.props.getChainTransaction();
        this.props.getTransactions();
        this.props.getTransactionsGraph();
    }


    fetchWebSocketData(socketData) {
        console.log("fetchWebSocketData before" + JSON.stringify(socketData));
        console.log("fetchWebSocketData before" + socketData.res);
        var tempBlockData = [];
        var tempBlockDetailArr = [];
        for (var j in socketData.res.data.data) {
            tempBlockDetailArr.push({
                'key': j,
                'txnNo': socketData.res.data.data[j].payload.header.channel_header.tx_id,
                'date': moment(socketData.res.data.data[j].payload.header.channel_header.timeStamp).startOf('day').fromNow()
            })
        }
        tempBlockData.push({
            'key': 0,
            'blockno': socketData.res.header.number,
            'transaction': socketData.res.data.data.length,
            'headerprev': socketData.res.header.previous_hash,
            'headerdata': socketData.res.header.data_hash,
            'date': moment(socketData.res.data.data[0].payload.header.channel_header.timeStamp).startOf('day').fromNow(),
            'detail': tempBlockDetailArr,
            'others': socketData.res
        })

        var existingData = this.state.blocksData;
        existingData.splice(0, 0, tempBlockData[0]);
        for (var i in existingData) {
            existingData[i].key = i;
        }
        console.log("fetchWebSocketData after" + JSON.stringify(existingData));
        if (tempBlockData !== undefined) {
            this.setState({ blocksData: existingData }, function () {
                console.log(this.state.blocksData);
            });
        }
    }

    fetchSocketChartData(data) {
        //setting Bar chart
        var barResponseData = data.res['transactionChart'];
        var barData = [];
        for (var index = 0; index < barResponseData.length; index++) {
            var barDataObj = {
                "Date": barResponseData[index].date,
                "count": barResponseData[index].counts,
                "DateColor": "hsl(16, 70%, 50%)",
            }
            barData.push(barDataObj)
        }

        var BarChartTempdates = Utils.Last7Days();
        BarChartTempdates.forEach(element => {
            if (!barData.some(item => item.Date === element)) {
                barData.push({ "Date": element, "counts": 0, "DateColor": "hsl(16, 70%, 50%)" })
            }
        });

        barData.sort(function (a, b) {
            var keyA = new Date(a.Date),
                keyB = new Date(b.Date);
            // Compare the 2 dates
            if (keyA < keyB) return -1;
            if (keyA > keyB) return 1;
            return 0;
        });
        //setting line chart  nextProps.transactionGraph['step']
        var lineChartData = data.res['step']
        // console.log("LINE BEFORE CHART DATA " + JSON.stringify(lineChartResponseData));
        var lineChartResponseData = [];
        for (var j = 0; j < lineChartData.length; j++) {
            var lineDataObj = {
                "id": lineChartData[j].id,
                "color": Utils.getColor(j).color,
                "data": lineChartData[j].data,
            }
            lineChartResponseData.push(lineDataObj)
        }


        var tempdates;
        lineChartResponseData.forEach(element => {
            tempdates = Utils.Last7Days()
            element.data.forEach(data => {
                tempdates.splice(tempdates.indexOf(data.x), 1)
            });
            tempdates.forEach(addDates => {
                element.data.push({ x: addDates, y: 0 })
            });
        });
        for (var len = 0; len < lineChartResponseData.length; len++) {
            lineChartResponseData[len].data.sort(function (a, b) {
                var keyA = new Date(a.x),
                    keyB = new Date(b.x);
                // Compare the 2 dates
                if (keyA < keyB) return -1;
                if (keyA > keyB) return 1;
                return 0;
            });
        }
        // console.log("LINE CHART DATA " + JSON.stringify(lineChartResponseData));
        lineChartResponseData.reverse()
        this.setState({
            transactionBarChart: barData,
            transactionLineChart: lineChartResponseData
        })
        this.setState({ loadingForGraph: false });

    }

    fetchSocketTransactionData(data){
        // console.log('Socket transaction'+data);
        if(data && data.res && data.res.obj){
            var tempTransaction = [];
            var transType = "" + data.res.obj.transactionType + "";
            var type = transType.split(".");
            var sentence = "" + data.res.obj.participantInvoking + "";
            var participant = sentence.split("#")[1];
            tempTransaction.push({ 
                'key': 0, 
                'txnNo': data.res.obj.transactionId, 
                'type': type[type.length - 1], 
                'participant': participant?participant:'-', 
                'event':data.res.obj.eventsEmitted,
                'others': data.res.obj 
                })
            var existingData = this.state.transactions;
            existingData.splice(0, 0, tempTransaction[0]);
            for (var i in existingData) {
                existingData[i].key = i;
            }
            // console.log("fetchWebSocketData after" + JSON.stringify(existingData));
            if (tempTransaction !== undefined) {
                this.setState({ transactions: existingData }, function () {
                    console.log(this.state.transactions);
                });
            }
            this.setState({ loadingForTransaction: false });
        }
    }


    componentWillReceiveProps(nextProps) {
        debugger;
        // console.log(nextProps);
        if (nextProps && nextProps.blocks && nextProps.blocks !== this.props.blocks) {
            // console.log(nextProps.blocks);
            var tempBlockData = [];
            if(nextProps.blocks.type !== "GET_BLOCKS_ERROR_RESULT"){
                for (var i in nextProps.blocks) {
                    var tempBlockDetailArr = [];
                    for (var j in nextProps.blocks[i].data.data) {
                        tempBlockDetailArr.push({
                            'key': j,
                            'txnNo': nextProps.blocks[i].data.data[j].payload.header.channel_header.tx_id,
                            'date': moment(nextProps.blocks[i].data.data[j].payload.header.channel_header.timeStamp).startOf('day').fromNow()
                        })
                    }
                    tempBlockData.push({
                        'key': i,
                        'blockno': nextProps.blocks[i].header.number,
                        'transaction': nextProps.blocks[i].data.data.length,
                        'headerprev': nextProps.blocks[i].header.previous_hash,
                        'headerdata': nextProps.blocks[i].header.data_hash,
                        'date': moment(nextProps.blocks[i].data.data[0].payload.header.channel_header.timeStamp).startOf('day').fromNow(),
                        'detail': tempBlockDetailArr,
                        'others': nextProps.blocks[i]
                    })
                    if (tempBlockData !== undefined) {
                        this.setState({ blocksData: tempBlockData }, function () {
                            // console.log(this.state.blocksData);
                        });
                    }
                    this.setState({ loadingForBlock: false });
                }
            }else{
                this.setState({ loadingForBlock: false });
                message.error('Block API Failed');
            }

        }

        if (nextProps && nextProps.transactionGraph && nextProps.transactionGraph !== this.props.transactionGraph) {
            if (nextProps.transactionGraph.type !== "GET_TRANSACTION_GRAPH_ERROR_RESULT") {

                debugger
                if (nextProps.transactionGraph['transactionChart'] !== undefined) {
                    //setting Bar chart
                    var barResponseData = nextProps.transactionGraph['transactionChart'];
                    var barData = [];
                    for (var index = 0; index < barResponseData.length; index++) {
                        var barDataObj = {
                            "Date": barResponseData[index].date,
                            "count": barResponseData[index].counts,
                            "DateColor": "hsl(16, 70%, 50%)",
                        }
                        barData.push(barDataObj)
                    }

                    var BarChartTempdates = Utils.Last7Days();
                    BarChartTempdates.forEach(element => {
                        if (!barData.some(item => item.Date === element)) {
                            barData.push({ "Date": element, "counts": 0, "DateColor": "hsl(16, 70%, 50%)" })
                        }
                    });

                    barData.sort(function (a, b) {
                        var keyA = new Date(a.Date),
                            keyB = new Date(b.Date);
                        // Compare the 2 dates
                        if (keyA < keyB) return -1;
                        if (keyA > keyB) return 1;
                        return 0;
                    });
                    //setting line chart  
                    var lineChartData = nextProps.transactionGraph['step']
                    // console.log("LINE BEFORE CHART DATA " + JSON.stringify(lineChartData));
                    var lineChartResponseData = [];
                    for (var index = 0; index < lineChartData.length; index++) {
                        var lineDataObj = {
                            "id": lineChartData[index].id,
                            "color": Utils.getColor(index).color,
                            "data": lineChartData[index].data,
                        }

                        var BarChartTempdates = Utils.Last7Days();
                        BarChartTempdates.forEach(element => {
                            if (!barData.some(item => item.Date === element)) {
                                barData.push({ "Date": element, "counts": 0, "DateColor": "hsl(16, 70%, 50%)" })
                            }
                        });

                        barData.sort(function (a, b) {
                            var keyA = new Date(a.Date),
                                keyB = new Date(b.Date);
                            // Compare the 2 dates
                            if (keyA < keyB) return -1;
                            if (keyA > keyB) return 1;
                            return 0;
                        });
                        //setting line chart  nextProps.transactionGraph['step']
                        var lineChartData = nextProps.transactionGraph['step']
                        // console.log("LINE BEFORE CHART DATA " + JSON.stringify(lineChartResponseData));
                        var lineChartResponseData = [];
                        for (var index = 0; index < lineChartData.length; index++) {
                            var lineDataObj = {
                                "id": lineChartData[index].id,
                                "color": Utils.getColor(index).color,
                                "data": lineChartData[index].data,
                            }
                            lineChartResponseData.push(lineDataObj)
                        }

                        var tempdates;
                        // console.log("LAST 7DAYS RECORD "+JSON.stringify(Utils.Last7Days()));
                        lineChartResponseData.forEach(element => {
                            tempdates = Utils.Last7Days()
                            element.data.forEach(data => {
                                tempdates.splice(tempdates.indexOf(data.x), 1)
                            });
                            tempdates.forEach(addDates => {
                                element.data.push({ x: addDates, y: 0 })
                            });
                        });
                        for (var len = 0; len < lineChartResponseData.length; len++) {
                            lineChartResponseData[len].data.sort(function (a, b) {
                                var keyA = new Date(a.x),
                                    keyB = new Date(b.x);
                                // Compare the 2 dates
                                if (keyA < keyB) return -1;
                                if (keyA > keyB) return 1;
                                return 0;
                            });
                        }
                        // console.log("LINE CHART DATA " + JSON.stringify(lineChartResponseData));
                        lineChartResponseData.reverse()
                        this.setState({
                            transactionBarChart: barData,
                            transactionLineChart: lineChartResponseData
                        })
                    }
                    // console.log("LINE CHART DATA " + JSON.stringify(lineChartResponseData));
                    lineChartResponseData.reverse()
                    this.setState({
                        transactionBarChart: barData,
                        transactionLineChart: lineChartResponseData
                    })
                    this.setState({ loadingForGraph: false });
                }

            } else {
                this.setState({ loadingForGraph: false });
                message.error('Chart API Failed');
            }
        }


        if (nextProps && nextProps.transactions && nextProps.transactions !== this.props.transactions) {
            // console.log(nextProps.transactions);
            if (nextProps.transactions.type !== "GET_TRANSACTION_ERROR_RESULT") {
                var tempTransaction = [];
                for (var i in nextProps.transactions.obj) {
                    var transType = "" + nextProps.transactions.obj[i].transactionType + "";
                    var type = transType.split(".");
                    var sentence = "" + nextProps.transactions.obj[i].participantInvoking + "";
                    var participant = sentence.split("#")[1];
                    tempTransaction.push({ 'key': i, 'txnNo': nextProps.transactions.obj[i].transactionId, 'type': type[type.length - 1], 'participant':participant?participant:'-', 'event': nextProps.transactions.obj[i].eventsEmitted, 'others': nextProps.transactions.obj[i] })
                }
                if (tempTransaction !== undefined) {
                    this.setState({ transactions: tempTransaction }, function () {
                        // console.log(this.state.transactions);
                    });
                }
                this.setState({ loadingForTransaction: false });
            } else {
                this.setState({ loadingForTransaction: false });
                message.error('Transaction API Failed');
            }
        }
    }



    selectedBlock = (record) => {
        debugger;
        // console.log("BLOCK CLICKED " + JSON.stringify(record))
        this.props.history.push(pathConstants.BLOCKDETAIL + '?block=' + record.blockno, {
            selectedRecord: record,
        });
        Utils.goToTop();
    }

    selectedTransaction = (record) => {
        var tempArr = [];
        // console.log("TRANS CLICKED " + JSON.stringify(record))
        for (var i in record.event) {
            var buyersentence = "" + record.event[i].buyer + "";
            var sellersentence = "" + record.event[i].seller + "";
            var buyer = buyersentence.split("#")[1];
            var seller = sellersentence.split("#")[1];
            tempArr.push({
                'key': i,
                "buyer": buyer,
                "seller": seller,
                'rId': record.event[i].receiptId,
                'step': record.event[i].step,
                'status': record.event[i].status,
                'class': record.event[i].$class
            })
        }
        this.props.history.push(pathConstants.TRANSACTION_DETAILS + '?txn=' + record.txnNo, {
            // selectedTransData : JSON.stringify(tempArr),
            transactionDetails: record
        })
        Utils.goToTop();
        // this.setState({loading:false});
    }

    viewAllBlocks = () => {
        debugger;
        this.props.history.push(pathConstants.BLOCKLIST, {
            blockData: this.state.blocksData
        })
        Utils.goToTop();
    }

    viewAllTransaction = () => {
        // this.props.history.push(pathConstants.TRANSACTIONS);
        this.props.history.push(pathConstants.TRANSACTIONS, {
            transData: this.state.transactions
        })
        Utils.goToTop();
    }


    render() {

        return (
            <div className="gutter-example">

                <Row gutter={16} className="blockexplorer__chartrow">
                    <Col className="gutter-row" lg={11} md={24} sm={24} xs={24}>
                        <div className="gutter-box blockexplorer__chart">
                            {

                                this.state.loadingForGraph ?
                                    <Loader />
                                    :
                                    this.state.transactionLineChart.length ?
                                        <LineChart dataSet={this.state.transactionLineChart} />
                                        : null
                            }
                        </div>
                    </Col>
                    <Col className="gutter-row" lg={1} xs={24}></Col>
                    <Col className="gutter-row" lg={11} md={24} sm={24} xs={24}>
                        <div className="gutter-box blockexplorer__chart">
                            {
                                this.state.loadingForGraph ?
                                    <Loader />
                                    :
                                    this.state.transactionBarChart.length ?
                                        <BarChart dataSet={this.state.transactionBarChart}
                                            keySet={APP_CONSTANTS.TRANSACTION_BAR_GRAPH_KEY_SET} />
                                        : null
                            }
                        </div>
                    </Col>
                </Row>

                <Row gutter={16} className="blockexplorer__blockTransaction">
                    <Col className="gutter-row blockexplorer__blockTransaction-col" lg={11} md={24} sm={24} xs={24}>
                        <Row>
                            <Col span={12}><h2 className="heading-primary">BLOCKS</h2></Col>
                            <Col span={12}><Button className="u_float_right" onClick={() => this.viewAllBlocks()}>View all</Button></Col>
                        </Row><br />
                        <div className="gutter-box">
                            {
                                this.state.loadingForBlock ?
                                    <Loader />
                                    :
                                    <Table className="blockexplorer__explorerTable" columns={APP_CONSTANTS.BlockTablecolumns} dataSource={this.state.blocksData.slice(0, 5)} pagination={false}
                                        onRow={(record) => ({ onClick: () => { this.selectedBlock(record); } })} />
                            }

                        </div>
                    </Col>
                    <Col className="gutter-row" lg={1} xs={24}></Col>
                    <Col className="gutter-row blockexplorer__blockTransaction-col" lg={11} md={24} sm={24} xs={24}>
                        <Row>
                            <Col span={12}><h2 className="heading-primary">TRANSACTIONS</h2></Col>
                            <Col span={12}><Button className="u_float_right" onClick={() => this.viewAllTransaction()}>View all</Button></Col>
                        </Row><br />

                        <div className="gutter-box">
                            {

                                this.state.loadingForTransaction ?
                                    <Loader />
                                    :
                                    <Table className="blockexplorer__explorerTable"  dataSource={this.state.transactions.slice(0, 5)} columns={APP_CONSTANTS.listcolumns} pagination={false}
                                        onRow={(record) => ({ onClick: () => { this.selectedTransaction(record); } })}
                                    />
                            }

                        </div>
                    </Col>
                </Row>
            </div>
        );
    }
}
function mapStateToProps(state) {
    return {
        ...state,
        blocks: state.blockExplorer.Blocks,
        transactions: state.blockExplorer.Transactions,
        transactionGraph: state.blockExplorer.TransactionGraph,
        chainTransaction: state.blockExplorer.chainTransactions
    }
}

export default connect(
    mapStateToProps,
    {
        getBlocks,
        getTransactions,
        getTransactionData,
        getTransactionsGraph,
        getChainTransaction
    })(withRouter(BlockExploreContainer));




