import React, { Component } from 'react';

//nivo charts
import { ResponsiveBar } from '@nivo/bar'
import './index.scss';


class BarChart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading:true,
            transactionGraph: this.props.dataSet,
            keys: this.props.keySet
        }
    }


    componentWillReceiveProps(nextProps) {
        // this.setState({loading:false});

        if (nextProps.dataSet !== this.state.transactionGraph)
            this.setState({ transactionGraph: nextProps.dataSet })

        if (nextProps.keySet !== this.state.keys)
            this.setState({ keys: nextProps.keySet })
       
    }

    render() {
        return (
           
            <div className="graphParent">
             {/* {
                this.state.loading? */}
                <ResponsiveBar
                    data={this.state.transactionGraph}
                    keys={this.state.keys}
                    indexBy="Date"
                    margin={{
                        "top": 20,
                        "right": 20,
                        "bottom": 80,
                        "left": 60
                    }}
                    padding={0.3}
                    colors="paired"
                    colorBy="id"
                    borderColor="inherit:darker(1.6)"
                    axisBottom={{
                        "orient": "bottom",
                        "tickSize": 5,
                        "tickPadding": 5,
                        "tickRotation": 90,
                        "legend": "",
                        "legendOffset": 36,
                        "legendPosition": "middle"
                    }}
                    axisLeft={{
                        "tickSize": 5,
                        "tickPadding": 5,
                        "tickRotation": 0,
                        "legend": "count",
                        "legendPosition": "middle",
                        "legendOffset": -40
                    }}
                    labelSkipWidth={5}
                    labelSkipHeight={12}
                    labelTextColor="inherit:darker(1.6)"
                    animate={true}
                    motionStiffness={90}
                    motionDamping={15}
                    legends={[
                        {
                            "anchor": "bottom-right",
                            "direction": "column",
                            "justify": false,
                            "translateX": 120,
                            "translateY": 0,
                            "itemsSpacing": 2,
                            "itemWidth": 100,
                            "itemHeight": 20,
                            "itemDirection": "left-to-right",
                            "itemOpacity": 0.85,
                            "effects": [
                                {
                                    "on": "hover",
                                    "style": {
                                        "itemOpacity": 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
                {/* :
                <Loader />
            } */}
            </div>
            
        );
    }
}
export default (BarChart);
