import React from 'react';
import { withRouter } from 'react-router';
import { connect } from 'react-redux'
import { Row, Col } from 'antd';

import { pathConstants } from 'constants/path-constants';
import CustomBox from 'components/custom-box';
// import Loader from 'components/loader';
import './index.scss';


// Actions
import { default as DashboardStatusActions } from "store/blockExplorer/actions";
import * as Sockets from '../../sockets/socket';

const { getDashboardStatus, getBlockTransaction } = DashboardStatusActions;
const APP_CONSTANTS = require('constants/app-constants')


class StatusCount extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: false,
            DashboardStatus: {},
        }
    }

    componentDidMount() {
        this.setState({ loading: true });
        APP_CONSTANTS.setBlockCount(0);
        this.props.getDashboardStatus();
       
        var self=this;
        Sockets.emitter.addListener('dashboard', function (data) {
            console.log("Dashboard SOCKET DATA RECEIVED" +JSON.stringify(data));
            self.fetchWebSocketData(data);
        });
    }

    componentWillReceiveProps(nextProps) {
        // console.log(nextProps);
        if (nextProps && nextProps.dashboardStatus && nextProps.dashboardStatus !== this.props.dashboardStatus) {
            this.setState({ DashboardStatus: nextProps.dashboardStatus }, function () {
                console.log(this.state.DashboardStatus);
            });
            APP_CONSTANTS.setBlockCount(nextProps.dashboardStatus.obj.latestBlock.number)
            console.log("BLOCK COUNT VALUE " + APP_CONSTANTS.LATEST_BLOCK_NO);
            this.setState({ loading: false });
        }
    }

    fetchWebSocketData(data){
        // data.res.obj
        if (data && data.res) {
            this.setState({ DashboardStatus: data.res }, function () {
                console.log(this.state.DashboardStatus);
            });
            APP_CONSTANTS.setBlockCount(data.res.obj.latestBlock.number)
            console.log("BLOCK COUNT VALUE " + APP_CONSTANTS.LATEST_BLOCK_NO);
            this.setState({ loading: false });
        }
    }

    viewStatusDetails = (id) =>{
        this.props.history.push(pathConstants.TRANSACTION_DETAILS+'?txn='+id)   
    }

    latestBlockDetails = (id, number) => {
        this.props.history.push(pathConstants.BLOCKDETAIL + '?block=' +number);
    }

    render() {
        return (
            <div className="status-count">
                <Row gutter={16}>
                    <Col className="gutter-row" md={6} sm={6} xs={24} span={6}>
                        <div className="gutter-box u_cursor_pointer">

                            <CustomBox title="Genesis Block" onClick={this.latestBlockDetails}
                                number={0}
                                count={
                                    this.state.DashboardStatus.obj && this.state.DashboardStatus.obj.genesisBlock ?
                                        this.state.DashboardStatus.obj.genesisBlock.data_hash
                                        : null
                                }
                            />

                        </div>
                    </Col>
                    <Col className="gutter-row" md={6} sm={6} xs={24} span={6}>
                        <div className="gutter-box u_cursor_pointer">
                            <CustomBox  title="Latest Tx#" onClick={this.viewStatusDetails}
                                number={''}
                                count={
                                    this.state.DashboardStatus.obj && this.state.DashboardStatus.obj.lastTransHash ?
                                        this.state.DashboardStatus.obj.lastTransHash
                                        : null
                                } />
                        </div>
                    </Col>
                    <Col className="gutter-row" md={6} sm={6} xs={24} span={6}>
                        <div className="gutter-box u_cursor_pointer">
                            <CustomBox  title="Latest Block" onClick={this.latestBlockDetails}
                                number={this.state.DashboardStatus.obj && this.state.DashboardStatus.obj.latestBlock ?
                                this.state.DashboardStatus.obj.latestBlock.number
                                : null}
                                count={
                                    this.state.DashboardStatus.obj && this.state.DashboardStatus.obj.latestBlock ?
                                        this.state.DashboardStatus.obj.latestBlock.data_hash
                                        : null
                                } />
                        </div>
                    </Col>
                    <Col className="gutter-row" md={6} sm={6} xs={24} span={6}>
                        <div className="gutter-box">
                            <CustomBox title="Chain Code"
                                number={''}
                                count={
                                    this.state.DashboardStatus.obj && this.state.DashboardStatus.obj.chainCode ?
                                        this.state.DashboardStatus.obj.chainCode
                                        : null
                                } />
                        </div>
                    </Col>
                </Row>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        ...state,
        dashboardStatus: state.blockExplorer.DashboardStatus,
        blockTransactionDetails: state.blockExplorer.Blocktransaction,
    }
}

export default connect(
    mapStateToProps,
    {
        getDashboardStatus,
        getBlockTransaction
    })(withRouter(StatusCount));


