import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Table, Pagination, message } from 'antd';
import { default as GetBlocksActions } from "store/blockExplorer/actions";
import Loader from 'components/loader';
import moment from "moment";

import * as Utils from 'utils/util';
import './index.scss';
import * as Sockets from '../../sockets/socket';

// constants
import { pathConstants } from 'constants/path-constants';
const NETWORK_CONSTANTS = require('constants/api-constants');
const APP_CONSTANTS = require('constants/app-constants');
const { getBlocks } = GetBlocksActions;
// var Url = "/chain/blocks?startIndex=" + {} + "&pageSize=10"
// var { EventEmitter } = require('fbemitter');
// var emitter;



class BlockList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      current: 1,
      blocksData: [],
    }
    this.tableData = this.tableData.bind(this);
  }

  componentDidMount() {
    var self = this;
    Sockets.emitter.addListener('block', function (data) {
      // console.log("BlockList SOCKET DATA RECEIVED" + JSON.stringify(data));
      self.fetchWebSocketData(data);
    });
    this.setState({ loading: true });
    this.props.getBlocks(NETWORK_CONSTANTS.BLOCKS_URL + "?startIndex=" + this.state.current);
  }


  fetchWebSocketData(webSocketData) {
    if(this.state.current !== 1){
      this.onChange(1)
      return;
    }
    var existingData = this.state.blocksData;
    var tempBlockData = [];  
    var tempBlockDetailArr = [];
    for (var j in webSocketData.res.data.data) {
      tempBlockDetailArr.push({
        'key': j,
        'txnNo': webSocketData.res.data.data[j].payload.header.channel_header.tx_id,
        'date': moment(webSocketData.res.data.data[j].payload.header.channel_header.timeStamp).format('DD/MM/YYYY')
      })
    }
    tempBlockData.push({
      'key': 0,
      'blockno': webSocketData.res.header.number,
      'transaction': webSocketData.res.data.data.length,
      'headerprev':webSocketData.res.header.previous_hash,
      'headerdata':webSocketData.res.header.data_hash,
      'date': moment(webSocketData.res.data.data[0].payload.header.channel_header.timeStamp).format('DD/MM/YYYY'),
      'detail': tempBlockDetailArr,
      'others': webSocketData.res
    })
    existingData.splice(0, 0, tempBlockData[0]);
    for (var i in existingData) {
          existingData[i].key=i;
    }
    if (tempBlockData !== undefined) {
      this.setState({ blocksData: existingData }, function () {
        console.log(this.state.blocksData);
      });
    }
  }



  componentWillReceiveProps(nextProps) {
    debugger;
    // console.log(nextProps);
    if (nextProps && nextProps.blocks && nextProps.blocks !== this.props.blocks) {
      console.log(nextProps.blocks);
      var tempBlockData = [];
      if(nextProps.blocks.type !== "GET_BLOCKS_ERROR_RESULT"){
      for (var i in nextProps.blocks) {
        var tempBlockDetailArr = [];
        for (var j in nextProps.blocks[i].data.data) {
          tempBlockDetailArr.push({
            'key': j,
            'txnNo': nextProps.blocks[i].data.data[j].payload.header.channel_header.tx_id,
            'date': moment(nextProps.blocks[i].data.data[j].payload.header.channel_header.timeStamp).startOf('day').fromNow()
          })
        }
        tempBlockData.push({
          'key': i,
          'blockno': nextProps.blocks[i].header.number,
          'transaction': nextProps.blocks[i].data.data.length,
          'headerprev':nextProps.blocks[i].header.previous_hash,
          'headerdata':nextProps.blocks[i].header.data_hash,
          'date': moment(nextProps.blocks[i].data.data[0].payload.header.channel_header.timeStamp).startOf('day').fromNow(),
          'detail': tempBlockDetailArr,
          'others': nextProps.blocks[i]
        })
        if (tempBlockData !== undefined) {
          this.setState({ blocksData: tempBlockData }, function () {
            console.log(this.state.blocksData);
          });
        }
        this.setState({ loading: false });
      }
    }else{
      this.setState({ loading: false });
      message.error('Block API Failed');
      this.props.history.push(pathConstants.BLOCKEXPLORER);
      Utils.goToTop();
    }

    }
  }

  selectRow = (record) => {
    debugger;
    // console.log("BLOCK CLICKED " + JSON.stringify(record))
    this.props.history.push(pathConstants.BLOCKDETAIL + '?block=' + record.blockno, {
      selectedRecord: record
    });
    Utils.goToTop();

  }

  onChange = (page) => {
    // console.log(page);
    var url = NETWORK_CONSTANTS.BLOCKS_URL + "?startIndex=" + page
    this.setState({
      current: page,
    });
    this.setState({ loading: true });
    debugger
    this.props.getBlocks(url);
    Utils.goToTop();
  }

  tableData() {
    return (
      <Row>
        <Table className="blocks-list__blocklistTable"  dataSource={this.state.blocksData}
          columns={APP_CONSTANTS.BlockListcolumns}
          pagination={false}
          onRow={(record) => ({ onClick: () => { this.selectRow(record); } })} />
        <br />
        <Row type="flex" justify="end">
          <Pagination
            current={this.state.current}
            onChange={this.onChange} total={APP_CONSTANTS.LATEST_BLOCK_NO} />
        </Row>
      </Row>
    );
  }

  render() {

    return (
      <div className="blocks-list">
        <h1 className="heading-primary">BLOCKS</h1>
        <Row>
          {
            this.state.loading ?
              <Loader className="blocks-list__verticalcenter" /> :
              this.tableData()
          }
        </Row>

      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    ...state,
    blocks: state.blockExplorer.Blocks,
  }
}

export default connect(
  mapStateToProps,
  {
    getBlocks,
  })(withRouter(BlockList));

