import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Table } from 'antd';
// Actions
import { default as GetBlocksActions } from "store/blockExplorer/actions";
import Loader from 'components/loader';
import * as Sockets from '../../sockets/socket';
import * as Utils from 'utils/util';

import { pathConstants } from 'constants/path-constants';
import './index.scss';
const APP_CONSTANTS = require('constants/app-constants')
const { getTransactions, } = GetBlocksActions;


class TransactionList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      transactions: [],
      currentpage: 1,
    }
  }

  componentDidMount() {
    this.setState({ loading: true });
    var self = this;
    Sockets.emitter.addListener('transactionInfo', function (data) {
      console.log("transactionInfo SOCKET DATA RECEIVED" + JSON.stringify(data));
      self.fetchWebSocketData(data);
    });
    this.props.getTransactions();
  }


  fetchWebSocketData(data) {
    if (this.state.currentpage !== 1) {
      this.onChangePage(1)
      return;
    }
    var existingData = this.state.transactions;
    var tempTransaction = [];
    var transType = "" + data.transactions.obj.transactionType + "";
    var type = transType.split(".");
    var sentence = "" + data.transactions.obj.participantInvoking + "";
    var participant = sentence.split("#")[1];
    tempTransaction.push({
      'key': 0,
      'txnNo': data.transactions.obj.transactionId,
      'type': type[type.length - 1],
      'participant': participant?participant:'-',
      'event': data.transactions.obj.eventsEmitted,
      'others': data.transactions.obj
    })

    existingData.splice(0, 0, tempTransaction[0]);
    for (var i in existingData) {
      existingData[i].key = i;
    } 
    if (tempTransaction !== undefined) {
      this.setState({ transactions: tempTransaction }, function () {
        console.log(this.state.transactions);
        console.log(this.state.transactions.length);
      });
    }
  }

  componentWillReceiveProps(nextProps) {

    if (nextProps && nextProps.transactions && nextProps.transactions !== this.props.transactions) {
      // console.log(nextProps.transactions);
      var tempTransaction = [];
      for (var i in nextProps.transactions.obj) {
        var transType = "" + nextProps.transactions.obj[i].transactionType + "";
        var type = transType.split(".");
        var sentence = "" + nextProps.transactions.obj[i].participantInvoking + "";
        var participant = sentence.split("#")[1];
        tempTransaction.push({ 'key': i, 'txnNo': nextProps.transactions.obj[i].transactionId, 'type': type[type.length - 1], 'participant': participant?participant:'-', 'event': nextProps.transactions.obj[i].eventsEmitted, 'others': nextProps.transactions.obj[i] })
      }
      if (tempTransaction !== undefined) {
        this.setState({ transactions: tempTransaction }, function () {
          console.log(this.state.transactions);
          console.log(this.state.transactions.length);

        });
      }
      this.setState({ loading: false });
    }
  }

  selectedTransaction = (record) => {
    var tempArr = [];
    console.log("TRANS CLICKED " + JSON.stringify(record))
    for (var i in record.event) {
      var buyersentence = "" + record.event[i].buyer + "";
      var sellersentence = "" + record.event[i].seller + "";
      var buyer = buyersentence.split("#")[1];
      var seller = sellersentence.split("#")[1];
      tempArr.push({
        'key': i,
        "buyer": buyer,
        "seller": seller,
        'rId': record.event[i].receiptId,
        'step': record.event[i].step,
        'status': record.event[i].status
      })
    }
    console.log('tempArr' + JSON.stringify(tempArr));
    this.props.history.push(pathConstants.TRANSACTION_DETAILS + '?txn=' + record.txnNo, {
      selectedTransData: JSON.stringify(tempArr),
      transactionDetails: record
    })
    Utils.goToTop();

  }

  onChangePage = (page) => {
    console.log(page);
    this.setState({
      currentpage: page,
    });
    // endpoint = '?startIndex='+page+'&pageSize=10';
    this.props.getTransactions(page);
    Utils.goToTop();
  }


  render() {
    // console.log(this.props.location.state.transData);
    console.log(this.state.transactions.length);
    return (
      <div className="blocks-details transaction-list">
        <h2 className="heading-primary">Transactions</h2>
        <Row>
          {
            this.state.loading ?
              <Loader />
              :
              <div>
                <Table className="transaction-list__listTable" dataSource={this.state.transactions}
                  columns={APP_CONSTANTS.listcolumns}
                  // pagination={false}
                  onRow={(record) => ({ onClick: () => { this.selectedTransaction(record); } })}
                />

                {/* <Pagination current={this.state.currentpage} onChange={this.onChangePage} total={this.state.transactions.length} /> */}
              </div>
          }

        </Row>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    ...state,
    transactions: state.blockExplorer.Transactions,
  }
}

export default connect(
  mapStateToProps,
  {
    getTransactions,
  })(withRouter(TransactionList));
// export default withRouter(TransactionList);
