import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col, Table} from 'antd';
// import moment from "moment";

import * as Utils from 'utils/util';
// import { pathConstants } from 'constants/path-constants';
import './index.scss';
// Actions
import { default as TransactionDetailActions } from "store/blockExplorer/actions";
// containers
import Loader from 'components/loader';


const APP_CONSTANTS = require('constants/app-constants')
const { getBlockTransaction } = TransactionDetailActions;


class TransactionDetails extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            showHeader:false,
            transactionDetails:[],
            loadingForTransaction:false,
        }

        this.selectedRecord=this.selectedRecord.bind(this);
    }

    componentDidMount() {
        this.setState({loadingForTransaction:true});
        var txnId =this.getUrlParam('txn');
        this.props.getBlockTransaction(
            {
                txn:txnId
            }
        )

    }
    componentWillReceiveProps(nextProps) {
        // console.log(nextProps.transactionDetails);
        if(nextProps && nextProps.transactionDetails && nextProps.transactionDetails !== this.props.transactionDetails){
        var tempTransaction =[];  
        
            var transType = ""+nextProps.transactionDetails.obj.transactionType+"";
            var type = transType.split(".");
            var sentence = ""+nextProps.transactionDetails.obj.participantInvoking+"";
            var participant = sentence.split("#")[1];
            tempTransaction.push({
                'key':0,
                'txnNo':nextProps.transactionDetails.obj.transactionId,
                'type':type[type.length-1],
                'participant':participant,
                'eventcount':nextProps.transactionDetails.obj.eventsEmitted.length,
                'event':nextProps.transactionDetails.obj.eventsEmitted,
                'others':nextProps.transactionDetails.obj
            })
        
            // console.log('tempTransaction'+JSON.stringify(tempTransaction));
            if(tempTransaction !== undefined){
                this.setState({transactionDetails: tempTransaction}, function () {
                    console.log(this.state.transactionDetails);
                });
            }
            this.setState({loadingForTransaction:false});
            
        }
    }

    selectedRecord(record){
        var data={
            pathname:'/receipts',
            state:{
                receiptId:record.rId,
            }
        }
        this.props.history.push(data);
        Utils.goToTop();
    }

    selectedTransaction(record) {
        var data = {
            pathname: "/receipts",
            state: {
                receiptId: record.rId
            }
        }
        this.props.history.push(data);
        Utils.goToTop();
    }

    getUrlParam = (id) => {
        var urlparameter;
        if(window.location.href.indexOf(id) > -1){
            urlparameter = this.getUrlVars()[id];
            }
        return urlparameter;
    }

    getUrlVars = () => {
        var vars = {};
        window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
            vars[key] = value;
        });
        return vars;
    }

    render(){
        // console.log(this.state.transactionDetails);
        const EventTableData = [];
        const transInfoData = [];
    
        const selectedData = this.state.transactionDetails;
        for(var i in selectedData){
        if(selectedData[i].event.length>0){
            for(var i in selectedData[i].event){
                if(selectedData[i].event[i].$class === "param.network.model.onStatusChanged"){
                    var buyersentence = ""+selectedData[i].event[i].buyer+"";
                    var sellersentence = ""+selectedData[i].event[i].seller+"";
                    var buyer = buyersentence.split("#")[1];
                    var seller = sellersentence.split("#")[1];
                    EventTableData.push({
                        'key':i,
                        'buyer':buyer,
                        'seller':seller,
                        'rId':selectedData[i].event[i].receiptId,
                        'step':selectedData[i].event[i].step,
                        'status':selectedData[i].event[i].status,
                        'class':selectedData[i].event[i].$class
                    })
                }
            }
        }

        if(selectedData[i].others){
            var entries = Object.entries(selectedData[i].others);
            // console.log(entries);
            for(var i in entries){
                if(entries[i][0] !== "eventsEmitted"){
                    transInfoData.push({
                        'key':i,
                        'headerkey':entries[i][0],
                        'value': entries[i][1]
                    })
                }
            }
           
        }
        }

        return(
            <div className="transaction-details">
            {
                this.state.loadingForTransaction?
                     <Loader />
                :
                <div>
                    <h2>Metadata</h2>
                    <Row>
                        <Col span={24} >
                            <Table className="transaction-details__metadata" {...this.state} 
                                    columns={APP_CONSTANTS.transInfoColumn}
                                    dataSource = {transInfoData}
                                    pagination={false}
                            />
                        </Col>
                    </Row>

                    {
                        EventTableData.length > 0?
                        
                            <div className="transaction-details__eventTable">
                                <h2>Param Transaction Events</h2>
                                <Table  className="transaction-details__paramTable" 
                                        columns={APP_CONSTANTS.transactionDetailColumn}  
                                        dataSource = { EventTableData }
                                        // pagination={false}
                                        onRow={(record) => ({ onClick: () => { this.selectedRecord(record); } })}
                                />
                                
                            </div>
                        :null
                    }
                </div>
            }
            </div>
        );
    }

    
}

function mapStateToProps(state) {
    return {
        ...state,
        transactionDetails: state.blockExplorer.Blocktransaction,

    }
}

export default connect(
    mapStateToProps,
    {
        getBlockTransaction,
    })(withRouter(TransactionDetails));

