import socketIOClient from "socket.io-client";

var { EventEmitter } = require('fbemitter');
export var emitter = new EventEmitter();
const NETWORK_CONSTANT = require('constants/api-constants');
export var socket,socketConnection;


export function connectToAuthWebsocket() {
    //Connecting to socket  
    socket = socketIOClient(NETWORK_CONSTANT.BASE_URL_WEB_SOCKET
        , { transports: ['websocket'] });
            
    socketConnection = socket.connect();
    console.log('socket'+socketConnection )
    
    //ON Block Data Received
    socket.on("block", function (data) {
        console.log("SOCKET DATA RECEIVED for block" + JSON.stringify(data))
        emitter.emit("block", data);
    });

    //ON Dashboard Data Received
    socket.on("dashboard", function (data) {
        console.log("SOCKET DATA RECEIVED for Dashboard" + JSON.stringify(data))
        emitter.emit("dashboard", data);
    });

    //ON CHART Data Received
    socket.on("chart", function (data) {
        console.log("SOCKET DATA RECEIVED for chart" + JSON.stringify(data))
        emitter.emit("chart", data);
    });

     //ON Transaction Data Received
     socket.on("transactionInfo", function (data) {
        console.log("SOCKET DATA RECEIVED for Transaction" + JSON.stringify(data))
        emitter.emit("transactionInfo", data);
    });
    
}
export function connectSocket(){
    debugger;
    socket = socketIOClient(NETWORK_CONSTANT.BASE_URL_WEB_SOCKET
        , { transports: ['websocket'] });
    socketConnection = socket.connect();
    return socketConnection;
}


