import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Table, message} from 'antd';
import './index.scss';
// Actions
// import { default as TransactionDetailActions } from "store/blockExplorer/actions";
import { default as GetBlocksActions } from "store/blockExplorer/actions";
import moment from "moment";
import * as Utils from 'utils/util';
// 
import Loader from 'components/loader';
// constants
import { pathConstants } from 'constants/path-constants';
// import Item from 'antd/lib/list/Item';
// import { isArray } from 'util';
const APP_CONSTANTS = require('constants/app-constants');
const NETWORK_CONSTANTS = require('constants/api-constants');
// const { getBlockTransaction } = TransactionDetailActions;
const { getBlocks } = GetBlocksActions;


class BlocksDetailList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      blockNo: '',
      showHeader: false,
      loading: false,
      headerMetaInfo: [],
      metaInfo: [],
      tableData: [],

    }
  }

  componentDidMount() {
    var blockNo = this.getUrlParam('block');
    console.log("BLOCK COUNT VALUE INSIDE BlocksDetailList " + APP_CONSTANTS.LATEST_BLOCK_NO);
    this.setState({
      blockNo: blockNo,
      loading: false,
      selectedData: [],
    });
    this.checkPropsData(blockNo);
  }

  componentWillReceiveProps(nextProps) {
    console.log(nextProps.blocks);
    console.log(nextProps.blocks.type);
    debugger;
    if(nextProps && nextProps.blocks && nextProps.blocks !== this.props.blocks){
      if(nextProps.blocks.type !== "GET_BLOCKS_ERROR_RESULT"){
          var tempBlockData = [];
          var tempBlockDetailArr = [];
          for (var j in nextProps.blocks.obj.data.data) {
              tempBlockDetailArr.push({
                  'key': j,
                  'txnNo': nextProps.blocks.obj.data.data[j].payload.header.channel_header.tx_id?nextProps.blocks.obj.data.data[j].payload.header.channel_header.tx_id:'(No hash)',
                  'date': moment(nextProps.blocks.obj.data.data[j].payload.header.channel_header.timeStamp).startOf('day').fromNow() 
              })
          }
          tempBlockData.push({
              'key': 0,
              'blockno': nextProps.blocks.obj.header.number,
              'transaction': nextProps.blocks.obj.data.data.length,
              'date': moment(nextProps.blocks.obj.data.data[0].payload.header.channel_header.timeStamp).startOf('day').fromNow(),
              'detail': tempBlockDetailArr,
              'others': nextProps.blocks.obj
          })
          if (tempBlockData !== undefined) {
            //console.log(tempBlockData);
            var blockData = tempBlockData[0];
              this.generateData(blockData.others.metadata.metadata,blockData.others.header,blockData.detail)
          }
          this.setState({ loading: false });
        }
        else{
          this.setState({ loading: false });
          message.error('Block Detail API Failed');
          this.props.history.push(pathConstants.BLOCKEXPLORER);
          Utils.goToTop();
          // alert('API Failed');
        }
    }

    
  }


  generateData(metaInfoValue, headerDataValue, tableDataValue) {
  
    const headerMetaInfoData = [];
    const metaInfoData = [];
    // console.log(this.props.location.state.selectedRecord.others);
    // console.log(this.props.location.state.selectedRecord.detail);

    const headerData = Object.entries(headerDataValue);
    headerData.map(function (item) {
      var i = 0;
      headerMetaInfoData.push({
        'key': i++,
        'headerkey': item[0],
        'value': item[1]?item[1]:'(No hash)'
      })
    })
    var metadata = metaInfoValue;
    metadata.map(function (item) {
      if (!Array.isArray(item)) {
        // console.log(item);
        if(item.signatures.length>0){
            var entries = Object.entries(item.signatures[0].signature_header.creator);
            // var nounce = item.signatures[0].signature_header.nounce;
            //  console.log(entries);
            //  console.log(nounce);
            for (var i in entries) {
              var key = entries[i][0];
              var value = entries[i][1];
              if (key === "IdBytes") {
                value = value.substring(0, 200)
              }
              metaInfoData.push({
                'key': i,
                'headerkey': key,
                'value': value,
              })
            }
        }
      }
    })

    this.setState({ headerMetaInfo: headerMetaInfoData, metaInfo: metaInfoData, tableData: tableDataValue })
  }

  checkPropsData(blockno) {
    try {
      if (undefined !== this.props.location.state.selectedRecord.others.header &&
          this.props.location.state.selectedRecord.others.metadata.metadata !== undefined &&
          this.props.location.state.selectedRecord.detail !== undefined
      ) {
        debugger;
        this.generateData(this.props.location.state.selectedRecord.others.metadata.metadata,
          this.props.location.state.selectedRecord.others.header,
          this.props.location.state.selectedRecord.detail);
        return;
      }
    } catch (error) {
        this.setState({ loading: true });
        this.props.getBlocks(NETWORK_CONSTANTS.BLOCK_URL + "?blockno=" + blockno);
    }
  }

  getUrlParam = (id) => {
    var urlparameter;
    if (window.location.href.indexOf(id) > -1) {
      urlparameter = this.getUrlVars()[id];
    }
    return urlparameter;
  }

  getUrlVars = () => {
    var vars = {};
    window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function (m, key, value) {
      vars[key] = value;
    });
    return vars;
  }

  selectedTransaction = (record) => {
    if(record.txnNo !== '(No hash)'){
      this.props.history.push(pathConstants.TRANSACTION_DETAILS+'?txn='+record.txnNo)
    }
    Utils.goToTop();
  }

  render() {
    console.log(this.state);
    debugger;
    return (
      <div className="blocks-details-list">

        {/* <h1 className="heading-primary">Block {this.state.blockNo}</h1> */}

        {
          this.state.loading ?
            <Loader />
            :
            <div>
              <Row>
                <div>
                  <h2>Header Info</h2>
                  <Table {...this.state} columns={APP_CONSTANTS.transInfoColumn}
                    dataSource={this.state.headerMetaInfo}
                    pagination={false}
                  />
                </div>
                {/* <Col span={24}>
                  <h2>Header Info</h2><br />
                  {
                    headerData.map( function(item){
                      return(             
                          <Row>
                            <Col className="blocks-details-list__headercol" span={4}><b>{item[0]}</b></Col>blocks
                            <Col className="blocks-details-list__headercol" span={20}>{item[1]}</Col>
                          </Row>
                      )
                  },this)
                  }
                </Col> */}
              </Row><br /><br /><br />

              <Row>
                <div>
                  <h2>Transactions Info</h2>
                  <Table className="blocks-details-list__listTable" columns={APP_CONSTANTS.blockDetailColumn} dataSource={this.state.tableData} pagination={false}
                    onRow={(record) => ({ onClick: () => { this.selectedTransaction(record); } })}
                  />
                </div>
              </Row><br /><br /><br />

              <Row>
                <div>
                  <h2>Metadata</h2>
                  <Table {...this.state} className="blocks-details-list__MetaInfoTable" columns={APP_CONSTANTS.blockInfoColumn}
                    dataSource={this.state.metaInfo}
                    pagination={false}
                  />
                </div>
              </Row>
            </div>
        }
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    ...state,
    // blockTransactionDetails: state.blockExplorer.Blocktransaction,
    blocks: state.blockExplorer.Blocks,
  }
}

export default connect(
  mapStateToProps,
  {
    getBlocks,
  })(withRouter(BlocksDetailList));

