import React  from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row,Table} from 'antd';
// import './index.scss';

// Actions
import { default as TransactionActions } from "store/blockExplorer/actions";

const APP_CONSTANTS = require('constants/app-constants')
const { getBlockTransaction } = TransactionActions;


class ReceiptDetails extends React.Component{
    // constructor(props){
    //     super(props);

    // }

    componentDidMount(){
        this.getUrlParam('id');
        debugger;
        this.props.getBlockTransaction();
    }
    
    componentWillReceiveProps(nextProps){
        debugger;
        console.log(nextProps);
    }
       
    getUrlParam = (id) => {
        var urlparameter;
        debugger;
        if(window.location.href.indexOf(id) > -1){
            urlparameter = this.getUrlVars()[id];
            }
        return urlparameter;
    }

    getUrlVars = () => {
        var vars = {};
         window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
            vars[key] = value;
        });
        return vars;
    }

    render() {
        return (
            <div className="blocks-details">
                <h2>Receipt Details</h2>
                <Row>
                   <Table dataSource={APP_CONSTANTS.listdata} columns={APP_CONSTANTS.listcolumns} pagination={false}
                        // onRow={(record) => ({ onClick: () => { this.selectedTransaction(record); } })}
                  /> 
                    {/* <Table dataSource={listdata} columns={listcolumns} pagination={false}/>  */}
                </Row>
            </div>
        );
    }
}
function mapStateToProps(state) {
	return {
		...state,
		blockTransactions: state.blockExplorer.Blocktransaction,
		
	}
}

export default connect(
	mapStateToProps,
	{
		getBlockTransaction,
	})(withRouter(ReceiptDetails));

